const SEND_MESSAGE = 'SEND-MESSAGE';
let initialState = {
  dialogs: [
    {
      id: 1,
      name: 'Vlados'
    }, {
      id: 2,
      name: 'Misha'
    }
  ],
  messages: [
    {
      id: 1,
      message: 'Hello hi'
    }, {
      id: 2,
      message: 'Привет, влад'
    }
  ],
};
let dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case SEND_MESSAGE:
      let messages = state.messages;
      let currentMessage = messages[messages.length - 1];
      let currentMessageId = currentMessage.id || 1;
      return {
        ...state,
        messages: [
          ...messages, {
            id: ++currentMessageId,
            message: action.message
          }
        ]
      }
    default:
      return state;
  }
}

export const sendMessage = function(message) {
  return {type: SEND_MESSAGE, message}
}
export default dialogsReducer;