import React from 'react';
import styles from '../utils/formControls.module.css'
const FormControl = ({input, el, meta: { touched, error, warning } , ...props}) => {
    const hasError = touched && error;
    return (<div className={hasError ? styles.error : null}>
        {React.createElement(el, {...input, ...props})}
    </div>)
}

export const Textarea = (props) => {
    return (<FormControl {...props} el={'textarea'}/>)
}
export const Input = (props) => {
    return (<FormControl {...props} el={'input'}/>)
}