import {getAuthProfileApi, getProfileInfoApi, logoutApi, signInApi} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'SET-USER-DATA';
let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    avatar: null,
};
let authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload
            }
        default:
            return state;
    }
}
export const setUserData = function (userId, email, login, avatar, isAuth) {
    return {type: SET_USER_DATA, payload: {userId, email, login, avatar, isAuth}}
}
export const getAuthProfile = () => {
    return async function (dispatch) {
        let data = await getAuthProfileApi();
        if (data.resultCode === 0) {
            let {id, email, login} = data.data;

            let result = await getProfileInfoApi(id)
            let avatar = result.photos.small;
            dispatch(setUserData(id, email, login, avatar, true));

        } else {
            throw new Error('Авторизационные данные не получены')
        }

    }
}

export const signIn = (formData) => {
    return async function (dispatch) {

        let data = await signInApi(formData);

        if (data.resultCode === 0) {
            dispatch(getAuthProfile())
        } else {
            dispatch(stopSubmit('loginForm', {_error: data.messages[0]}))
        }

    }
}
export const logout = () => {
    return async function (dispatch) {
        let data = await logoutApi()

        if (data.resultCode === 0) {
            dispatch(setUserData(null, null, null, null, false))
        }
    }
}


export default authReducer;