import * as axios from "axios";

let instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": '98066f73-404d-4706-9089-8e2198c6a16a'
    }
})
export const getUsersApi = (pageNumber = 1, pageSize = 100) => {
    return instance.get(`users?page=${pageNumber}&count=${pageSize}`).then(response => response.data)
}
export const followApi = (userId) => {
    return instance.post(`follow/${userId}`).then(response => response.data)
}
export const unfollowApi = (userId) => {
    return instance.delete(`follow/${userId}`).then(response => response.data)
}
export const getProfileInfoApi = (userId) => {
    return instance.get(`profile/${userId}`)
        .then(response => response.data)
}
export const getAuthProfileApi = () => {
    return instance.get(`auth/me`).then(response => response.data)
}
export const getUserStatusApi = (userId) => {
    return instance.get(`profile/status/${userId}`).then(response => response.data)
}
export const updateUserStatusApi = (status) => {
    return instance.put(`profile/status`, {status}).then(response => response.data)
}
export const signInApi = (data) => {
    return instance.post(`auth/login`, {...data}).then(response => response.data)
}
export const logoutApi = () => {
    return instance.delete(`auth/login`).then(response => response.data)
}


