import React from 'react';
import {Field, reduxForm} from "redux-form";
import {maxLength, required} from "../../utils/validators";
import {Input} from "../../utils/formControls";
import {Redirect} from "react-router-dom";
import styles from '../../utils/formControls.module.css'
const maxLength50 = maxLength(50);

const LoginForm = (props) => {
    const {error, handleSubmit, submitting} = props
    return (
        <div>
            <form onSubmit={handleSubmit}>
                <Field name="Email" placeholder={'Email'} component={Input} type="text" validate={[required, maxLength50] }/>
                <Field name="Password" placeholder={'Password'} component={Input} type="password" validate={[required, maxLength50] }/>
                <div>{error && <strong className={styles.errorBlock}>{error}</strong>}</div>
                <button type="submit" disabled={submitting}>Submit</button>
            </form>
        </div>
    )
}

const LoginReduxForm = reduxForm({
    form: 'loginForm'
})(LoginForm)

const Login = (props) => {
    const submit = (values) => {
        props.signIn(values);
    }

    if (props.isAuth) return <Redirect to={'/profile'}/>


    return (<div>
        <LoginReduxForm onSubmit={submit} logout={props.logout}/>
    </div>);
}
export default Login;