import React from 'react';

import {
    getUsers,
    followRequest,
    unfollowRequest,
} from '../../redux/users-reducer';
import {connect} from 'react-redux';
import Users from "./Users";
import Preloader from "../common/Preloader/Preloader";
import {
    getCurrentPage,
    getFetchingStatus,
    getFollowingProgress,
    getPageSize,
    getTotalUsersCount,
    getUsersAll,
} from "../../redux/users-selectors";

class UsersApiContainer extends React.Component {

    componentDidMount() {
        const {currentPage,pageSize} = this.props;
        if (this.props.users.length === 0) {
            this.props.getUsers(currentPage, pageSize)
        }
    }

    toggleFollow = (userId, type) => {
        if (type === 'follow') {
            this.props.follow(userId);
        } else {
            this.props.unfollow(userId);
        }
    }

    onPageChanged = (page) => {
        const {pageSize} = this.props;
        this.props.getUsers(page, pageSize)
    }

    render() {
        return <>
            {this.props.isFetching && <Preloader/>}

            <Users totalUsersCount={this.props.totalUsersCount}
                   pageSize={this.props.pageSize}
                   users={this.props.users}
                   currentPage={this.props.currentPage}
                   onPageChanged={this.onPageChanged}
                   follow={this.toggleFollow}
                   unfollow={this.toggleFollow}
                   followingInProgress={this.props.followingInProgress}
            />
        </>
    }
}


const mapStateToProps = (state) => {
    return {
        users: getUsersAll(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getFetchingStatus(state),
        followingInProgress: getFollowingProgress(state)
    }
}
// 1. Коннект возвращает контейнер, в который прокидывает переданные пропсы
// 2. при загрузке страницы, после рендера, вызывается componentDidMount, в котором мы вызываем переданный пропс setFollowingStatus
// 3. Вызов такого пропса возвращает объект action, который внутри connect диспатчится, вызывается соответствующий reducer

// const mapDispatchToProps = (dispatch) => {
//     return {
//         getUsers : function() {
//             dispatch(setFollowingStatus)
//         }
//     }
// }


const UsersContainer = connect(mapStateToProps, {
    follow: followRequest,
    unfollow: unfollowRequest,
    getUsers
})(UsersApiContainer);
export default UsersContainer;