import React from 'react';

import User from "./User/User";
import './Users.css';
import Paginator from "../common/Paginator/Paginator";

const Users = ({totalUsersCount, pageSize, users, onPageChanged, currentPage, follow, unfollow, followingInProgress, ...props}) => {
    const getUsers = (users) => {
        return users.map((user) => {
            return (<User key={user.id} followed={user.followed} userName={user.name} photoUrl={user.photos.small}
                          signature={user.status} id={user.id} follow={() => {
                follow(user.id, 'follow')
            }} unfollow={() => {
               unfollow(user.id, 'unfollow')
            }} followingInProgress={followingInProgress}/>)
        })
    }

    let siteUsers = getUsers(users);
    return (<div className="content">
        <Paginator totalUsersCount={totalUsersCount} pageSize={pageSize} onPageChanged={onPageChanged} currentPage={currentPage}/>
        {siteUsers}
    </div>);

}

export default Users;