import React, {useState} from 'react';

const Paginator = ({totalUsersCount, pageSize, currentPage, onPageChanged, portionPage = 10, ...props}) => {
    let pagesCount = Math.ceil(totalUsersCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }
    let portionSize = Math.ceil(pagesCount / portionPage);
    let [currentPortion, setCurrentPortion] = useState(1)
    let leftButtonDistance = (currentPortion - 1) * portionPage + 1;
    let rightButtonDistance = currentPortion * portionPage;


    return (
        <div className="pagination">
            {currentPortion > 1 && (<button onClick={() => {
                setCurrentPortion(currentPortion - 1)
            }}>Prev
            </button>)}
            {pages
                .filter((page) => {
                    if (page >= leftButtonDistance && page <= rightButtonDistance) {
                        return page;
                    }
                    return false;
                })
                .map((pageNumber) => {
                    return <span key={pageNumber} className={currentPage === pageNumber ? 'active' : null}
                                 onClick={() => {
                                     onPageChanged(pageNumber);
                                 }}>{pageNumber}</span>
                })}
            {currentPortion < portionSize && (<button onClick={() => {
                setCurrentPortion(currentPortion + 1)
            }}>Next
            </button>)}


        </div>);

}

export default Paginator;