import {getProfileInfoApi, getUserStatusApi, updateUserStatusApi} from "../api/api";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET-USER-PROFILE';
const SET_USER_STATUS = 'SET-USER-STATUS';
const CHANGE_USER_STATUS = 'CHANGE-USER-STATUS';
let initialState = {
    posts: [
        {
            id: 1,
            message: "Hi, my friend!"
        }, {
            id: 2,
            message: "Hi, my friend!2"
        }, {
            id: 3,
            message: "Hi, my friend!3"
        }, {
            id: 4,
            message: "Hi, my friend!4"
        }
    ],
    profile: null,
    status: null,
};
let profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            let posts = state.posts;
            let currentPost = posts[posts.length - 1];
            let currentPostId = currentPost.id || 1;
            return {
                ...state,
                posts: [
                    ...posts, {
                        id: ++currentPostId,
                        message: action.message
                    }
                ],
                newPostText: ''
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            };
        case SET_USER_STATUS:
            return {
                ...state,
                status: action.status
            };
        case CHANGE_USER_STATUS:
            return {
                ...state,
                status: action.status
            };
        default:
            return state;
    }
}

export const AddPost = function (message) {
    return {type: ADD_POST, message}
}
const setUserProfile = function (profile) {
    return {type: SET_USER_PROFILE, profile}
}
const setStatus = function (status) {
    return {type: SET_USER_STATUS, status}
}


export const getProfileInfo = (userId) => async (dispatch) => { // стрелочна функция, которая возвращает другую стрелочную функцию
    let data = await getProfileInfoApi(userId);
    dispatch(setUserProfile(data));
}

export const getUserStatus = (userId) => async (dispatch) => { // стрелочна функция, которая возвращает другую стрелочную функцию
    let statusText = await getUserStatusApi(userId);
    dispatch(setStatus(statusText));

}
export const updateUserStatus = (status) => async (dispatch) => { // стрелочна функция, которая возвращает другую стрелочную функцию
    let statusNew = await updateUserStatusApi(status);
    if (statusNew.resultCode === 0) {
        dispatch(setStatus(status));
    }
}


export default profileReducer;