import React from 'react';
// import MyPostsContainer from './MyPosts/MyPostsContainer';
import avatar from '../../../assets/images/noavatar.png';
import './User.css';
import {NavLink} from "react-router-dom";

function User(props) {

    return (<div className="users__user">
        <div className="user__left">
            <div className="user__avatar">
                <NavLink to={'/profile/' + props.id}>
                    <img src={props.photoUrl !== null
                        ? props.photoUrl
                        : avatar} alt=""/>
                </NavLink>
            </div>
            <div className="user__follow">
                {
                    props.followed
                        ? <button onClick={props.unfollow} disabled={props.followingInProgress.some(id => id === props.id)}>Unfollow</button>
                        : <button onClick={props.follow} disabled={props.followingInProgress.some(id => id === props.id)}>Follow</button>
                }
            </div>
        </div>
        <div className="user__right">
            <div className="user__info">
                <div className="user__name">{props.userName}</div>
                <div className="user__location">{"props.location.country"}, {"props.location.city"}</div>
            </div>
            <div className="user__signature">
                <p>{props.signature}</p>
            </div>
        </div>
    </div>);
}

export default User;