import {followApi, getUsersApi, unfollowApi} from "../api/api";
import {updateObjectInArray} from "../utils/object-helpers";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET-USERS';
const CLEAR_USERS = 'CLEAR-USERS';
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE';
const SET_TOTAL_USERS_COUNT = 'SET-TOTAL-USERS-COUNT';
const SET_FETCHING_STATUS = 'SET-FETCHING-STATUS';
const SET_FOLLOWING_STATUS = 'SET-FOLLOWING-STATUS';
let initialState = {
    users: [],
    pageSize: 100,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [],
};
let usersReducer = (state = initialState, action) => {
    switch (action.type) {

        case FOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, 'id', {followed: true})
            }
        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, 'id', {followed: false})
            }
        case SET_USERS:
            return {
                ...state,
                users: [
                    // ...state.users,
                    ...action.users
                ]
            }
        case CLEAR_USERS:
            return {
                ...state,
                users: []
            }
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.numberPage
            }
        case SET_TOTAL_USERS_COUNT:
            return {
                ...state,
                totalUsersCount: action.usersCount
            }
        case SET_FETCHING_STATUS:
            return {
                ...state,
                isFetching: action.status
            }
        case SET_FOLLOWING_STATUS:
            return {
                ...state,
                followingInProgress: action.status
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id !== action.userId)
            }
        default:
            return state;
    }
}
let follow = (userId) => {
    return {type: FOLLOW, userId}
}
let unfollow = (userId) => {
    return {type: UNFOLLOW, userId}
}
let setUsers = (users) => {
    return {type: SET_USERS, users}
}
let setCurrentPage = (numberPage) => {
    return {type: SET_CURRENT_PAGE, numberPage}
}
let clearUsers = () => {
    return {type: CLEAR_USERS}
}
let setTotalUsersCount = (usersCount) => {
    return {type: SET_TOTAL_USERS_COUNT, usersCount}
}
let setFetchingStatus = (status) => {
    return {type: SET_FETCHING_STATUS, status}
}
let setFollowingStatus = (status, userId) => {
    return {type: SET_FOLLOWING_STATUS, status, userId}
}

export const getUsers = (page, pageSize) => {
    return async function (dispatch) {
        dispatch(setFetchingStatus(true));
        dispatch(setCurrentPage(page));

        let users = await getUsersApi(page, pageSize)

        dispatch(setUsers(users.items));
        dispatch(setTotalUsersCount(users.totalCount));
        dispatch(setFetchingStatus(false));

    }
}


const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
    try {
        dispatch(setFollowingStatus(true, userId));
        let data = await apiMethod(userId)

        if (data.resultCode === 0) {
            dispatch(actionCreator(userId));
        }
        dispatch(setFollowingStatus(false, userId));
    } catch (err) {
        dispatch(setFollowingStatus(false, userId));
    }
}


export const followRequest = (userId) => {
    return async function (dispatch) {
        followUnfollowFlow(dispatch, userId, followApi, follow)
    }
}
export const unfollowRequest = (userId) => {
    return async function (dispatch) {
        followUnfollowFlow(dispatch, userId, unfollowApi, unfollow)
    }
}


// создаем обертку над функцией, которую наша обертка возвращает. Передаем в возвращаемую функцию параметры, посредством замыкания


export default usersReducer;